import { useParams } from "react-router-dom";
import { useEffect, useState } from "react";
import { Box, Flex, Text } from "@chakra-ui/layout";
import { Select } from "@chakra-ui/react";
import { useFormik } from "formik";
import {
  FormControl,
  FormLabel,
  FormErrorMessage,
  Input,
} from "@chakra-ui/react";
import booking_bg from "../Background/booking_bg.jpg";
import Tabs from "../Component/tabs";
import styled from "@emotion/styled";
export default function Booking() {
  const { kind } = useParams();
  const [course, setCourse] = useState("");
  const [courseOption, setCoureOption] = useState([
    "",
    "",
    "",
    "",
    "",
  ]);
  const validate = (values) => {
    const errors = {};
    if (!values.email) {
      errors.email = "";
    } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)) {
      errors.email = "";
    }
    return errors;
  };
  const formik = useFormik({
    initialValues: {
      name: "",
      phone: "",
      email: "",
      people: 1,
      date: "",
      time: "",
      course: "",
    },
    validate,
    onSubmit: (values) => {
      console.log(values);
    },
  });
  useEffect(() => {
    var temp = courseOption;
    var test = temp[0];
    switch (kind) {
      case "lemon":
        test = temp.splice(0, 1);
        break;
      case "strawberry":
        test = temp.splice(1, 1);
        break;
      case "maffin":
        test = temp.splice(2, 1);
        break;
      case "puff":
        test = temp.splice(3, 1);
        break;
      default:
        test = temp.splice(4, 1);
        break;
    }
    setCourse(test);
    setCoureOption(temp);
  }, []);
  return (
    <Box bgImg={booking_bg} bgColor="#fff9e1" h="100vh" bgSize={"100% auto"}>
      <Tabs page="2" />
      <Box
        w="50vw"
        position="absolute"
        top="30vh"
        left="8vw"
        fontFamily="GenSenRounded"
      >
        <form onSubmit={formik.handleSubmit}>
          <Flex mb="3vh">
            <FormControl isRequired>
              <Flex>
                <CustomLabel>:</CustomLabel>
                <Input
                  w="210px"
                  name="name"
                  placeholder=""
                  onChange={formik.handleChange}
                  value={formik.values.name}
                />
              </Flex>
            </FormControl>
            <FormControl isRequired>
              <Flex>
                <CustomLabel>:</CustomLabel>
                <Input
                  w="210px"
                  name="phone"
                  placeholder=""
                  onChange={formik.handleChange}
                  value={formik.values.phone}
                />
              </Flex>
            </FormControl>
          </Flex>

          <FormControl isRequired isInvalid={formik.errors.email} mb="3vh">
            <Flex>
              <CustomLabel>Email:</CustomLabel>
              <Input
                w="41vw"
                name="email"
                type="email"
                placeholder=""
                onChange={formik.handleChange}
                value={formik.values.email}
              />
            </Flex>
            <FormErrorMessage>{formik.errors.email}</FormErrorMessage>
          </FormControl>
          <FormControl isRequired>
            <Flex mb="3vh">
              <CustomLabel>:</CustomLabel>
              <Select
                name="people"
                placeholder="1"
                w="230px"
                onChange={formik.handleChange}
              >
                <option value={formik.values.people}>2</option>
                <option value={formik.values.people}>3</option>
                <option value={formik.values.people}>4</option>
              </Select>
            </Flex>
          </FormControl>
          <Flex mb="3vh">
            <FormControl isRequired>
              <Flex>
                <CustomLabel>:</CustomLabel>
                <Input
                  w="230px"
                  name="date"
                  type="date"
                  onChange={formik.handleChange}
                  value={formik.values.date}
                />
              </Flex>
            </FormControl>
            <FormControl isRequired>
              <Flex>
                <CustomLabel>:</CustomLabel>
                <Select
                  name="time"
                  placeholder="11:00-13:00"
                  w="230px"
                  onChange={formik.handleChange}
                >
                  <option value={formik.values.time}>15:00-17:00</option>
                  <option value={formik.values.time}>19:00-21:00</option>
                </Select>
              </Flex>
            </FormControl>
          </Flex>

          <FormControl isRequired>
            <Flex mb="4vh">
              <CustomLabel>:</CustomLabel>
              <Select
                name="course"
                placeholder={course}
                w="35vw"
                onChange={formik.handleChange}
              >
                {courseOption &&
                  courseOption.map((e, index) => (
                    <option key={index} value={formik.values.course}>
                      {e}
                    </option>
                  ))}
              </Select>
            </Flex>
          </FormControl>
        </form>
        <Box>
          <Text color="#eaab99">
            
            <br />
            ,
            <br />
            
            <br />
             24
            
            <br />
             15
            
          </Text>
        </Box>
      </Box>
    </Box>
  );
}
const CustomLabel = styled(FormLabel)`
  font-size: 22px;
  color: #5c554b;
  font-weight: bold;
`;
